import React, { Component, Suspense } from 'react';
import { COPY_TO_CLIPBOARD } from '../../../Services/Utils/utils';

class DisplayUrlCopy extends Component {
    constructor() {
        super();
        this.state = { codeCopied: false };
    }
    codeCopy = (id) => {
        this.setState({
            codeCopied: true
        })
        setTimeout(() => {
            this.setState({
                codeCopied: false
            })
        }, 1500);
        COPY_TO_CLIPBOARD(id);
    }
    render() {
        const { codeCopied } = this.state
        const { url } = this.props
        return <div className="d-flex align-items-start mb-3 flex-wrap" style={{ maxWidth: 500 }}>
                    <div className="d-flex flex-column flex-grow-1 pe-3 mb-2">
                        <div className="position-relative">
                            <input type="text" name="displayUrl" id="displayUrlCopy_" onClick={() => { this.codeCopy('displayUrlCopy_') }} className="form-control form-control-lg fw-semibold text-ellipsis pe-5" value={url} readOnly />
                        </div>
                        <label className="fw-semibold fs-8 text-muted">Copy URL</label>
                    </div>
                    <button type="button" className="btn btn-primary btn-lg" onClick={() => { this.codeCopy('displayUrlCopy_') }} style={{minWidth:130}}>
                        <span className="svg-icon svg-icon-6">
                            {!codeCopied ?
                                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 18.17 18.17">
                                    <path id="Path_2" data-name="Path 2" d="M-932.08-463.83a2.92,2.92,0,0,1-2.92-2.92v-9.086a2.924,2.924,0,0,1,1.947-2.752v9.567a3.245,3.245,0,0,0,3.245,3.244h9.567a2.924,2.924,0,0,1-2.753,1.947Zm3.245-3.244a2.92,2.92,0,0,1-2.92-2.921v-9.085a2.92,2.92,0,0,1,2.92-2.92h9.086a2.92,2.92,0,0,1,2.92,2.92V-470a2.92,2.92,0,0,1-2.92,2.921Z" transform="translate(935 482)" fill="currentColor" />
                                </svg>
                                :
                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="20" height="20">
                                    <path fill="none" d="M0 0h24v24H0z" />
                                    <path d="M10 15.172l9.192-9.193 1.415 1.414L10 18l-6.364-6.364 1.414-1.414z" fill="currentColor" />
                                </svg>
                            }
                        </span>
                        {!codeCopied ? 'Copy Url' : 'Url Copied'}
                    </button>
                </div>

    }
}

export default DisplayUrlCopy;